.result-name-card{
    /* display: flex;
    justify-content: center; */
    
    display: flex;
    justify-content: space-around;
    color: #e7e9f0;
    background-color: #282d4f;
    margin: 20px;
    min-width: 75px;
    padding: 10px 20px;
    border-radius: 10px;
    box-shadow: 0px 0px 5px rgba(red, green, blue, 0.1);
    transition: background 0.35s;
}

.result-name-card:hover{
    background: #4a5079;
    box-shadow: rgba(red, green, blue, 0.2);

}

.result-name{
    margin: 0;
    font-weight: 100;
    font-size: 25px;
}